class App extends React.Component {
  state = { ratio: window.innerWidth / window.innerHeight };
  render() {
    const mobile = this.state.ratio < 1;
    return (
      <div>
        <Parallax pages={days.length} ref={(ref) => (this.parallax = ref)}>
          {days.map((day, i) => (
            <div key={i}>
              <ParallaxLayer
                offset={i}
                style={{
                  background: day.background ? `url("${day.background}")` : day.color,
                  backgroundSize: 'cover',
                }}
              ></ParallaxLayer>
              <ParallaxLayer offset={i} speed={0.3}>
                <img
                  src={day.illust.img}
                  alt={day.illust.name}
                  className="illust"
                  style={mobile && day.illust.mobile ? day.illust.mobile : day.illust.style}
                />
              </ParallaxLayer>
              <ParallaxLayer offset={i} speed={0.5}>
                <div className="page-text" style={mobile && day.mobile ? day.mobile : day.style}>
                  <h1>
                    <img alt={day.title} src={day.icon} />
                    {day.title}
                  </h1>
                  <h2>{day.text}</h2>
                </div>
              </ParallaxLayer>
            </div>
          ))}
        </Parallax>
      </div>
    )
  }
  updateDimensions = () => {
    this.setState({ ratio: window.innerWidth / window.innerHeight });
  }
  componentDidMount() {
    window.addEventListener('resize', this.updateDimensions);
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.updateDimensions);
  }
}

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
)
